<template class="pageContent">
  <Loading :status="loadingStatus"></Loading>
  <section class="bg-white">
    <section class="container">
      <!-- & -->
      <nav style="--bs-breadcrumb-divider: '>'"
        aria-label="breadcrumb"
        class="py-4">
        <ol class="breadcrumb m-0">
          <li class="breadcrumb-item">
            <router-link to="/" class="link-secondary"></router-link>
          </li>
          <li class="breadcrumb-item">
            <router-link to="/products" class="link-secondary"
              ></router-link
            >
          </li>
          <li class="breadcrumb-item active" aria-current="page">
            {{ tempProduct.title }}
          </li>
        </ol>
      </nav>

      <!--  -->
      <section class="row mb-1 pb-5 align-items-center">
        <!--  -->
        <div class="col-lg-6 col-xl-5">
          <img
            class="rounded-2 w-100"
            :src="productImg"
            :alt="tempProduct.title">
          <!-- :  -->
          <div class="col-12
            py-1
            flex-nowrap
            overflow-auto
            d-xxl-none">
            <div class="row flex-nowrap m-0">
              <div class="col-2 pe-1">
                <img
                :src="tempProduct.imageUrl"
                :alt="tempProduct.title"
                class="w-100 rounded pointer"
                @click="changeImg(tempProduct.imageUrl)">
              </div>

              <div class="col-2 pe-1"
                v-for="(img, key) in tempProduct.imagesUrl"
                :key="`_${key}`">
                <img
                  :src="img"
                  :alt="`_${key}`"
                  class="w-100 p-0 rounded pointer"
                  @click="changeImg(img)">
              </div>
            </div>
          </div>
        </div>
        <!-- :  -->
        <div
        class="col-1 p-1 img_view
          overflow-auto
          d-none d-xxl-block">
          <img
            :src="tempProduct.imageUrl"
            :alt="tempProduct.title"
            class="w-100 mb-2 p-0 rounded-1 pointer"
            @click="changeImg(tempProduct.imageUrl)">
          <img
            v-for="(img, key) in tempProduct.imagesUrl"
            :key="`_${key}`"
            :src="img"
            :alt="`_${key}`"
            class="w-100 mb-2 p-0 rounded-1 pointer"
            @click="changeImg(img)">
        </div>

        <!--  -->
        <div class="col-lg-6 col-xl-7 col-xxl-6
          py-2 ps-xl-3 ps-xxl-4">
          <h1 class="mb-3">{{ tempProduct.title }}</h1>
          <span
            class="border rounded text-dark px-3 py-1">
            {{ tempProduct.category }}
          </span>
          <p class="p-4 my-4 bg-primary-100">
            {{ tempProduct.content }}
          </p>

          <div class="text-end">
            <span class="fst-italic text-decoration-line-through text-gray">
              $ {{ tempProduct.origin_price }}
            </span>
            <p class="h3 fw-bold mb-2">
               $1500
            </p>

            <div
              class="row
              align-items-center
              flex-row-reverse
              mx-0 mb-2">
              <div class="col-6 p-0">
                <div class="input-group">
                  <button
                  type="button"
                  class="w-25
                    btn_light_green
                    text-center
                    border"
                    @click="changeNum('reduce')">
                    -
                  </button>

                  <input type="number"
                    class="form-control
                    m-0 p-1
                    text-center
                    border-0
                    border-top
                    border-bottom"
                    v-model.number="qty" min="1">

                  <button
                    type="button"
                    class="w-25
                      btn_light_green
                      text-center
                      border"
                    @click="changeNum('add')">
                    +
                    </button>

                </div>
              </div>
            </div>

            <div
              class="row
              align-items-center
              flex-row-reverse
              mx-0 mb-2">

              <div class="col-6 p-0">
                <button type="button"
                  class="btn btn_main py-1 w-100"
                  @click="addCart">
                  
                </button>
              </div>

              <template class="col-2 px-2 btn text-end">
                <i class="bi bi-heart-fill btn_red"></i>
              </template>
            </div>
            <p class="text-danger"> 5% </p>
          </div>
        </div>
      </section>
    </section>
  </section>

  <!--  -->
  <section class="row py-5">
    <section class="col-10 my-2 py-5 container text-center bg-white">
      <h2 class="pb-2 fw-bold text-primary"></h2>
      <div class="row justify-content-center">
        <span class="col-11 col-md-9 col-lg-6">
          <p>{{ tempProduct.description }}</p>
        </span>
      </div>
    </section>
  </section>
</template>

<script>
import swal from 'sweetalert';
import bus from '../components/bus';

export default {
  data() {
    return {
      loadingStatus: false, // false
      productId: '',
      productImg: '',
      tempProduct: {},
      qty: Number,
      cartsLength: Number,
    };
  },
  methods: {
    getProduct() { // 
      const url = `${process.env.VUE_APP_PATH}/api/${process.env.VUE_APP_API}/product/${this.productId}`;
      this.loadingStatus = true;

      this.$http.get(url)
        .then((res) => {
          if (res.data.success) {
            console.log('(-) res:', res);
            this.tempProduct = res.data.product;
            this.productImg = this.tempProduct.imageUrl;
            this.loadingStatus = false;
          } else {
            console.log('(-) res:', res);
            this.loadingStatus = false;
          }
        })
        .catch((err) => {
          console.log('(-) res:');
          console.dir(err);
          this.loadingStatus = false;
        });
    },
    changeNum(action) { // 
      if (action === 'reduce') {
        console.log('reduce');
        if (this.qty < 2) {
          this.swalFn(' 1', 'error');
          return;
        }
        this.qty -= 1;
      } else if (action === 'add') {
        console.log('add');
        this.qty += 1;
      }
    },
    addCart() { // 
      const url = `${process.env.VUE_APP_PATH}/api/${process.env.VUE_APP_API}/cart`;
      const cartData = {
        data: {
          product_id: this.productId,
          qty: this.qty,
        },
      };
      this.loadingStatus = true;

      this.$http
        .post(url, cartData)
        .then((res) => {
          if (res.data.success) {
            console.log('(-) res:', res);
            this.swalFn(res.data.message, 'success');
            this.qty = 1;
            this.updateCartLength();
            this.loadingStatus = false;
          } else {
            console.log('(-) res:', res);
            this.swalFn(res.data.message, 'error');
            this.qty = 1;
            this.loadingStatus = false;
          }
        })
        .catch((err) => {
          console.log('(-) err:');
          console.dir(err);
          this.qty = 1;
          this.loadingStatus = false;
        });
    },
    updateCartLength() { // 
      const url = `${process.env.VUE_APP_PATH}/api/${process.env.VUE_APP_API}/cart`;

      this.$http
        .get(url)
        .then((res) => {
          if (res.data.success) {
            let totleQty = 0;
            this.cartsLength = res.data.data.carts.forEach((item) => {
              totleQty += item.qty;
            });
            this.cartsLength = totleQty;
            bus.emit('cart-number', this.cartsLength);
          } else {
            console.log('(-) res:', res);
          }
        })
        .catch((err) => {
          console.log('(-) err:');
          console.dir(err);
        });
    },
    changeImg(img) { // 
      this.productImg = img;
    },
    swalFn(title, icon, timer = 1500, text, button = false) { // 
      // success ()  error ()  warning()  info ()
      const txt = {
        title,
        text,
        icon,
        button,
        timer,
        closeOnClickOutside: false,
      };
      swal(txt);
    },
  },
  created() {
    this.productId = this.$route.params.id;
    this.getProduct(this.productId);
    this.qty = 1;
  },
};
</script>
